export default "<template>\r\n  <div class=\"tpl\">\r\n    <raw-loader :mytxt=\" require('!raw-loader!./TabulatorModalAjax.vue').default \"></raw-loader>\r\n    <h1>This is Tabulator2</h1>\r\n    <div>\r\n      <div class=\"mb-1\">\r\n        <button class=\"mr-1\" @click=\"onAddModal\" >Add</button>\r\n      </div>\r\n      <!-- {{msg}} -->\r\n      <div ref=\"table\"></div>   \r\n      \r\n      <b-modal ref=\"my-modal\" size=\"sm\" :no-close-on-backdrop=\"true\" hide-footer :title=\" title \">\r\n      <div class=\"d-block\">\r\n        <form @submit.stop.prevent=\"onSaveModal\">\r\n          <div class=\"text-right\">\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\">Id</label>\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  readonly\r\n                  name=\"Id\"\r\n                  v-model=\"sel.Id\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('Id'),\r\n                  }\"\r\n                  type=\"text\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Name[{{ !errors.has(\"Name\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"Name\"\r\n                  v-model=\"sel.Name\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('Name'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n\r\n\r\n<!--\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Món[{{ !errors.has(\"monid\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <v-select\r\n                  :class=\"{ 'ko-hop-le-select2': errors.has('monid') }\"\r\n                  v-validate=\"'required'\"\r\n                  @input=\"onMonChange()\"\r\n                  name=\"monid\"\r\n                  v-model=\"sel.monid\"\r\n                  :reduce=\"(option) => option.id\"\r\n                  :options=\"\r\n                    mons.map((x) => ({\r\n                      id: x.id,\r\n                      label: `${x.id} - ${x.tenmon}`,\r\n                    }))\r\n                  \"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Đơn giá[{{ !errors.has(\"dongia\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"dongia\"\r\n                  readonly\r\n                  v-model=\"sel.dongia\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('dongia'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Số lượng[{{ !errors.has(\"solg\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"solg\"\r\n                  v-model=\"sel.solg\"\r\n                  @change=\"solgChange()\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('solg'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required|numeric'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Tiền[{{ !errors.has(\"tien\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <input\r\n                  name=\"tien\"\r\n                  readonly\r\n                  v-model=\"sel.tien\"\r\n                  :class=\"{\r\n                    'form-control': true,\r\n                    'ko-hop-le': errors.has('tien'),\r\n                  }\"\r\n                  type=\"text\"\r\n                  v-validate=\"'required|numeric'\"\r\n                />\r\n              </div>\r\n            </div>\r\n\r\n            <div class=\"form-group row\">\r\n              <label class=\"col-sm-4 col-form-label\"\r\n                >Bàn[{{ !errors.has(\"banid\") }}]</label\r\n              >\r\n              <div class=\"col-sm-8\">\r\n                <v-select\r\n                  :class=\"{ 'ko-hop-le-select2': errors.has('banid') }\"\r\n                  v-validate=\"'required'\"\r\n                  @input=\"onMonChange()\"\r\n                  name=\"banid\"\r\n                  v-model=\"sel.banid\"\r\n                  :reduce=\"(option) => option.id\"\r\n                  :options=\"\r\n                    bans.map((x) => ({\r\n                      id: x.id,\r\n                      label: `${x.id} - ${x.tenban}`,\r\n                    }))\r\n                  \"\r\n                />\r\n              </div>\r\n            </div>\r\n-->\r\n\r\n\r\n            <button type=\"submit\" class=\"btn mr-1\">Save</button>\r\n            <button type=\"button\" class=\"btn\" @click=\"hideModal()\">\r\n              Cancel\r\n            </button>\r\n          </div>\r\n        </form>\r\n      </div>\r\n    </b-modal>\r\n      \r\n\r\n\r\n    </div>\r\n  </div>\r\n</template>\r\n<script>\r\nimport {TabulatorFull as Tabulator} from 'tabulator-tables';\r\nimport $ from \"jquery\";\r\nimport axios from 'axios';\r\n\r\nimport VeeValidate from \"vee-validate\";\r\nimport Vue from \"vue\";\r\nVue.use(VeeValidate, {\r\n  classes: true,\r\n});\r\n\r\n\r\n\r\nexport default {\r\n  name: 'Ban',\r\n  components: {\r\n  },\r\n  data(){\r\n    return {\r\n      msg:\"thuc 101\",\r\n      tabulator: null, //variable to hold your table\r\n      tableData: [], //data for table to display\r\n\r\n      msg:\"\",\r\n      sel:{},\r\n      title:\"\",\r\n    }\r\n  },\r\n  mounted(){\r\n      let that=this;\r\n      this.tabulator = new Tabulator(this.$refs.table, {\r\n        pagination:true, //enable pagination\r\n        paginationMode:\"remote\", //enable remote pagination\r\n        ajaxSorting:true,\r\n        //ajaxURL:\"/api/Ban/GetPage\", //set url for ajax request\r\n        ajaxURL: \"https://randomuser.me/api\",\r\n        ajaxFiltering:true,\r\n        filterMode:\"remote\", //send filter data to the server instead of processing locally\r\n\r\n\r\n\r\n        ajaxParams:{token:\"ABC123\"}, //set any standard parameters to pass with the request\r\n        paginationSize:2, //optional parameter to request a certain number of rows per page\r\n        paginationInitialPage:1, //optional parameter to set the initial page to load    \r\n        paginationSizeSelector: [5, 10, 20, 30, 50],\r\n        height:$(window).innerHeight()-400,//'200px',//$(window).innerHeight()\r\n        //fitColumns:true,    \r\n        layout:\"fitData\",  \r\n        //layout:\"fitDataFill\",\r\n        //layout:\"fitColumns\",\r\n        index:\"Id\",\r\n        //autoColumns:true,\r\n\r\n        columns: [\r\n          { \r\n            title: \"Email\", \r\n            field: \"email\",\r\n            headerFilter: true,\r\n \r\n          },\r\n          { title: \"Cell\", field: \"cell\" },\r\n          { title: \"Gender\", field: \"gender\" },\r\n          { title: \"Phone\", field: \"phone\" },\r\n        ],\r\n\r\n\r\n        ajaxURLGenerator: function (url, config, params) {\r\n          //console.log(params);\r\n          const { page, size } = params;\r\n          return url + `?page=${page}&results=${size}`;\r\n\r\n          //return url + \"?params=\" + encodeURI(JSON.stringify(params)); //encode paramters as a json object\r\n\r\n\r\n        },\r\n        ajaxResponse: function (url, params, response) {\r\n          // Must configure with server side\r\n          console.log(params);\r\n          let last_page = 10;\r\n          return {\r\n            data: response.results,\r\n            last_page,\r\n          };\r\n        },\r\n\r\n\r\n\r\n        /*\r\n        columns:[                 //define the table columns\r\n          {\r\n            title: \"STT\",\r\n            formatter: \"rownum\",\r\n            headerHozAlign: \"center\",\r\n            hozAlign: \"center\",\r\n          },\r\n          {\r\n              title: \"Cmd\",\r\n              headerHozAlign: \"center\",\r\n              hozAlign: \"center\",\r\n              headerSort: false,\r\n              width: 90,\r\n              formatter: function (cell, formatterParams, onRendered) {\r\n                onRendered(function () {\r\n                  $(cell.getElement())\r\n                    .find(\"#edit\")\r\n                    .on(\"click\", cell.getRow(), that.onEdit);\r\n                  $(cell.getElement())\r\n                    .find(\"#del\")\r\n                    .on(\"click\", cell.getRow(), that.onDel);\r\n                });\r\n                let $div = $(\"<div>\");\r\n                $div.append(\r\n                  $(\"<button>\", { text: \"Edit\", class: \"mr-1\" }).attr(\"id\",\"edit\")\r\n                );\r\n                $div.append(\r\n                  $(\"<button>\", { text: \"Del\", class: \"mr-1\" }).attr(\"id\", \"del\")\r\n                );\r\n                return $div[0];\r\n              },\r\n          },\r\n          {\r\n            title:\"Id\", \r\n            field:\"Id\", \r\n            width:80,\r\n            headerHozAlign: \"center\",\r\n            hozAlign: \"center\",\r\n          },\r\n          {\r\n            title:\"Name\", \r\n            field:\"Name\", \r\n            //headerFilter: true,\r\n            headerFilter:\"input\",\r\n            width:200,\r\n            headerHozAlign: \"center\",\r\n            hozAlign: \"left\",\r\n          },\r\n        ],\r\n         */\r\n      });\r\n      this.tabulator.on(\"dataLoaded\", function(data){\r\n        that.msg={cmd:\"dataLoaded\",data:data};\r\n        let $tabu=$(that.$refs.table);\r\n        let $span = $(\"<span id='total' class='mr-1'>\").text(\"[total]\");\r\n        if($tabu.find(\".tabulator-footer-contents\").find(\"span#total\").length==0){\r\n          $tabu.find(\".tabulator-footer-contents span.tabulator-paginator\").prepend($span);\r\n        }  \r\n        let $myAddBtn=$(\"<button id='addBtn' class='mr-1'>MyAdd</button>\");\r\n        if($tabu.find(\".tabulator-footer-contents\").find(\"#addBtn\").length==0){\r\n          $myAddBtn.on(\"click\",function(){\r\n            alert(\"hello myAddBtn\");\r\n          })\r\n          $tabu.find(\".tabulator-footer-contents span.tabulator-paginator\").prepend($myAddBtn);\r\n        }  \r\n      });\r\n      this.tabulator.on(\"pageLoaded\", function(pageno){\r\n      });      \r\n  },\r\n  watch: {\r\n    tableData: {\r\n      handler: function (newData) {\r\n        this.tabulator.replaceData(newData)\r\n      },\r\n      deep: true\r\n    }\r\n  },  \r\n  created(){\r\n  },\r\n  methods:{\r\n    onDataLoaded(data){\r\n      let ar=[];\r\n      data.forEach((x,i)=>{\r\n        ar.push({x:x.id});\r\n      })\r\n      let curPage= this.tabulator.getPage();\r\n      this.msg={ar:ar,curPage:curPage};\r\n    },\r\n    onAddModal() {\r\n      let r={\r\n        Id:0,\r\n        Name:\"Thuc 101\",\r\n      }\r\n      this.sel=JSON.parse(JSON.stringify(r));\r\n      this.title=\"Add Bàn\";\r\n      this.showModal();\r\n    },\r\n\r\n    onSaveModal() {\r\n      this.$validator.validateAll().then((result) => {\r\n        if (!result) {\r\n          this.toast(\"Không hợp lệ\");\r\n          return;\r\n        }\r\n        let r=JSON.parse(JSON.stringify(this.sel));\r\n        //let ngay = moment(r.ngay, \"DD/MM/YYYY\").format(\"YYYY-MM-DD\");\r\n        //r.ngay = ngay;\r\n        //this.toast(JSON.stringify(r)); //alert(JSON.stringify(r));\r\n        this.Save(r).then((res) => {\r\n          if (res.data.kq == \"ok\") {\r\n            this.toast(res.data.msg);\r\n            this.hideModal();\r\n            this.setPage(this.getPage());\r\n          } else {\r\n            this.toast(res.data.msg);\r\n          }\r\n        });\r\n      });\r\n    },\r\n\r\n\r\n\r\n\r\n    async Save(r) {\r\n      return await axios.post(`/api/Ban/Save`, r).catch((err) => {\r\n        console.log(err);\r\n      });\r\n    },\r\n    async Del(id) {\r\n      return await axios\r\n        .get(`/api/Ban/Delete?id=${id}`)\r\n        .catch((err) => {\r\n          console.log(err);\r\n        });\r\n    },\r\n    async getById(id) {\r\n      return await axios.get(`/api/Ban/GetById?id=${id}`).catch((err) => {\r\n        console.log(err);\r\n      });\r\n    },\r\n\r\n\r\n\r\n    onDel(e){\r\n      // let row = e.data;\r\n      // let id = row.getIndex();\r\n      // let data=row.getData();\r\n      // this.msg={cmd:\"del\",id:id,data:data}\r\n\r\n      let row = e.data;\r\n      let id = row.getIndex();\r\n      this.Del(id).then((res) => {\r\n        if (res.data.kq == \"ok\") {\r\n          if (this.getData().length != 1) {\r\n            this.setPage(this.getPage());\r\n          } else {\r\n            this.setPage(this.getPage() - 1);\r\n          }\r\n        }\r\n        this.toast(res.data.msg);\r\n      });\r\n\r\n\r\n\r\n    },\r\n    onEdit(e){\r\n      // let row = e.data;\r\n      // let id = row.getIndex();\r\n      // let data=row.getData();\r\n      // this.msg={cmd:\"edit\",id:id,data:data}\r\n\r\n      let row = e.data;\r\n      let id = row.getIndex();\r\n      this.getById(id).then(res=>{\r\n        this.sel=res.data;\r\n        //$(this.$refs[\"my-modal\"]).attr(\"title\",\"helllsfsfsf\");\r\n        this.title=\"Edit Bàn\";\r\n        this.showModal();\r\n      })\r\n\r\n\r\n\r\n    },  \r\n    getData() {\r\n      return this.tabulator.getData();\r\n    },\r\n    \r\n    getPage() {\r\n      return this.tabulator.getPage();\r\n    },\r\n    setPage(_page) {\r\n      this.tabulator.setPage(_page);\r\n    },\r\n    \r\n    toast(msg, variant = null) {\r\n      this.$bvToast.toast(msg, {\r\n        title: `Information`,\r\n        variant: variant,\r\n        solid: true,\r\n      });\r\n    },\r\n\r\n\r\n    showModal() {\r\n      this.$refs[\"my-modal\"].show();\r\n    },\r\n    hideModal() {\r\n      this.$refs[\"my-modal\"].hide();\r\n    },\r\n\r\n\r\n\r\n  }\r\n}\r\n</script>\r\n<style lang=\"scss\">\r\n  $textSize: 8.5px; //14px !default; //table text size\r\n  @import \"~tabulator-tables/src/scss/tabulator.scss\";\r\n  .tabulator-col-title {\r\n    text-align: center;\r\n  }\r\n  .tabulator .tabulator-footer .tabulator-page.active {\r\n    background-color: greenyellow !important;\r\n    color: blue !important;\r\n  }\r\n  .select2,\r\n  .select2-selection {\r\n    width: 100% !important;\r\n  }\r\n  .select2-container .select2-selection--single {\r\n    height: 27px;\r\n  }\r\n  .select2-container--default .select2-selection--single .select2-selection__rendered {\r\n    color: #444;\r\n    line-height: 26px;\r\n  }\r\n  .datepicker td,\r\n  th {\r\n    text-align: center;\r\n    padding: 0px 6px;\r\n    font-size: 10px;\r\n  }\r\n  .col-4 input {\r\n    margin-bottom: 1px;\r\n  }\r\n  .my-invalid .select2-container {\r\n    border: 1px solid red;\r\n    border-radius: 5px;\r\n  }\r\n  .form-group {\r\n    margin-bottom: 0.3rem;\r\n  }\r\n  .col-form-label {\r\n    padding-top: calc(0.375rem + 1px);\r\n    padding-bottom: calc(0.375rem + 1px);\r\n    margin-bottom: 0;\r\n    font-size: inherit;\r\n    line-height: 1.5;\r\n    font-weight: bold;\r\n  }\r\n  \r\n  .vs__dropdown-toggle {\r\n    -webkit-appearance: none;\r\n    -moz-appearance: none;\r\n    appearance: none;\r\n    background: none;\r\n    border: var(--vs-border-width) var(--vs-border-style) var(--vs-border-color);\r\n    border-radius: var(--vs-border-radius);\r\n    display: flex;\r\n    padding: 0 0 0px;\r\n    white-space: normal;\r\n    /*border: 1px solid red;*/\r\n  }\r\n  .ko-hop-le-select2 {\r\n    border: 1px solid red;\r\n    border-radius: 10px;\r\n  }\r\n  .ko-hop-le{\r\n    border: 1px solid red;\r\n    border-radius: 10px;\r\n  }\r\n  </style>\r\n  \r\n";